import { Component, Host, Method, Prop, h } from '@stencil/core';
import { IconName } from '../../atoms/icon/icon.types';
import { BaseModalId } from '../base-modal/base-modal.types';

/**
 * Lark-theme overlay component.
 */
@Component({
  tag: 'bs-light-overlay',
  styleUrl: 'light-theme-overlay.scss',
  shadow: false, // no shadow DOM to make focus-trap work with slots
})
export class BsLightOverlay {
  private modalRef: HTMLBsBaseModalElement;

  /** Props */

  /**
   * Does the content contain multiple screens?
   */
  @Prop() screens?: boolean = false;

  /** Methods */
  /**
   * This method triggers the close method on the modal.
   */
  @Method()
  async closeOverlay() {
    this.modalRef.close();
  }

  /**
   * This method triggers the previous slide method on the modal.
   */
  @Method()
  async previousSlide() {
    this.modalRef.previousSlide();
  }

  /**
   * This method triggers the next slide method on the modal.
   */
  @Method()
  async nextSlide() {
    this.modalRef.nextSlide();
  }

  /**
   * This method triggers the handle focus method on the modal.
   */
  @Method()
  async focusTrap() {
    this.modalRef.handleFocus();
  }

  render() {
    return (
      <Host>
        <bs-base-modal
          ref={(el) => {
            this.modalRef = el;
          }}
          modalId={BaseModalId.Media}
          transitionType="fade"
        >
          <div class="light-overlay">
            <div class="light-overlay__content">
              {/* Head */}
              <bs-container class="light-overlay__head" contained={true}>
                <bs-click-action
                  name="close-light-overlay"
                  tagClass="light-overlay__close-icon"
                  onBsClickAction={this.closeOverlay.bind(this)}
                >
                  <bs-icon name={IconName.Close} />
                </bs-click-action>
              </bs-container>

              {/* Body */}
              <div class="light-overlay__body">
                {this.screens && (
                  <bs-click-action
                    name="previous-media"
                    tagClass="light-overlay__navigation-icon light-overlay__navigation-icon--previous"
                    onBsClickAction={this.previousSlide.bind(this)}
                  >
                    <bs-icon name={IconName.CaretForward} />
                  </bs-click-action>
                )}
                <bs-container contained={true}>
                  <slot name="media"></slot>
                </bs-container>
                {this.screens && (
                  <bs-click-action
                    name="next-media"
                    tagClass="light-overlay__navigation-icon light-overlay__navigation-icon--next"
                    onBsClickAction={this.nextSlide.bind(this)}
                  >
                    <bs-icon name={IconName.CaretForward} />
                  </bs-click-action>
                )}
              </div>

              {/* Footer */}
              <bs-container class="light-overlay__footer" contained={true}></bs-container>
            </div>
          </div>
        </bs-base-modal>
      </Host>
    );
  }
}
