:host {
  @include reset-host;
  --grid-main-image-height: 250px;
  --grid-main-image-padding: 20px;
  --grid-support-image-height: 0;
  --grid-gap: 0;

  @include mq('min', 'lg') {
    --grid-main-image-height: 435px;
    --grid-main-image-padding: 90px;
    --grid-support-image-height: 0;
    --grid-gap: 0;
  }
}

:host .badge-detail {
  display: flex;
  flex-direction: column;
  gap: rem(20);

  @include mq('min', 'lg') {
    flex-direction: row-reverse;
    gap: rem(40);
    height: calc(
      var(--grid-main-image-height) + var(--grid-support-image-height) + var(--grid-gap)
    );
  }
}

:host .image-grid {
  display: grid;
  grid-template-areas:
    'one one'
    'two three';
  grid-template-rows: var(--grid-main-image-height) var(--grid-support-image-height);
  grid-template-columns: 1fr 1fr;
  gap: var(--grid-gap);
  width: 100%;

  @include mq('min', 'lg') {
    width: rem(420);
  }
}

:host .image {
  --image--fit: contain;
  --image--height: calc(var(--grid-support-image-height) - 20px);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: rem(20);
  overflow: hidden;
  background-color: #eff1f1;

  &:nth-child(1) {
    --image--height: calc(var(--grid-main-image-height) - (2 * var(--grid-main-image-padding)));
    grid-area: one;
  }

  &:nth-child(2) {
    grid-area: two;
  }

  &:nth-child(3) {
    grid-area: three;
  }
}

:host .content {
  position: relative;
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: auto;
}

:host .cover {
  @include mq('min', 'lg') {
    @include cover;
    padding-right: rem(10);
  }
}

:host .button {
  color: $red-50;
}
