.light-overlay {
  height: 100vh;
  overflow: auto;
  background-color: $black-opacity-60;
  backdrop-filter: blur(3px);

  .light-overlay__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: rem(1032);
    min-height: 100vh;
    margin: 0 auto;
  }

  .light-overlay__head {
    @include mq('min', 'lg') {
      position: relative;
      flex-grow: 1;
    }
  }

  .light-overlay__close-icon {
    @include z-index('header');
    position: absolute;
    top: rem(10);
    right: rem(10);
    padding: rem(6);
    font-size: rem(20);
    color: $black;
    background-color: transparent;
    border: 0;
    border-radius: 50%;
    outline: none;

    @include mq('min', 'md') {
      top: rem(20);
      right: rem(20);
      font-size: rem(24);
    }

    @include mq('min', 'lg') {
      top: unset;
      right: 0;
      bottom: rem(20);
      padding: rem(7);
      font-size: rem(24);
      color: $white;
      border: 1px solid currentColor;
    }

    &:focus {
      background-color: rgba($gray-800, 0.5);
    }
  }

  .light-overlay__navigation-icon {
    position: absolute;
    top: 50%;
    padding: rem(7);
    font-size: rem(24);
    color: $white;
    background-color: transparent;
    border: 1px solid currentColor;
    border-radius: 50%;
    @include z-index('base');
    outline: none;

    &:focus {
      background-color: rgba($gray-800, 0.5);
    }

    &--previous {
      left: rem(-12);
      transform: rotate(180deg) translateY(50%);
      transform-origin: left center;
    }

    &--next {
      right: rem(-12);
      transform: translateY(-50%) translateX(100%);
    }

    @include mq('max', 'lg') {
      display: none;
    }
  }

  .light-overlay__body {
    position: relative;
    display: flex;
    flex-grow: 1;
    align-items: top;
    width: 100%;
    height: 100%;
    padding: rem(40) 0;
    background-color: $white;

    @include mq('min', 'lg') {
      box-sizing: border-box;
      flex-grow: 0;
      padding: rem(64);
    }
  }

  .light-overlay__footer {
    @include mq('min', 'lg') {
      flex-grow: 1;
    }
  }
}
