import { Component, Host, Prop, h } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { TitleSize, TitleTag } from '../../atoms/title/title.types';
import { TextSize } from '../../atoms/text/text.types';
import { BsImage } from '../../atoms/image/image';
import { BsButton } from '../../atoms/button/button';

/**
 * Test badge detail component.
 */
@Component({
  tag: 'bs-test-badge-detail',
  styleUrl: 'test-badge-detail.scss',
  shadow: true,
})
export class BsTestBadgeDetail {
  /** Props */
  /** The badge title. */
  @Prop() badgeTitle: string;

  /** The badge content. */
  @Prop() badgeText: string;

  /** The badge CTA. */
  @Prop() badgeCta: BsButton | string;

  @Memoize('badgeCta')
  get parsedBadgeCta(): BsButton {
    return parseIfString(this.badgeCta);
  }

  /**
   * The test badge image.
   */
  @Prop() badgeImages: BsImage[] | string;

  @Memoize('badgeImages')
  get parsedbadgeImages(): BsImage[] {
    return parseIfString(this.badgeImages);
  }

  render() {
    return (
      <Host>
        <div class="badge-detail">
          <div class="content">
            <div class="cover">
              <bs-title
                class="title"
                text={this.badgeTitle}
                tag={TitleTag.Span}
                size={TitleSize.Head3}
              ></bs-title>
              <bs-text class="text" text={this.badgeText} size={TextSize.Default}></bs-text>
              {this.parsedBadgeCta.text && (
                <bs-button class="button" {...this.parsedBadgeCta}></bs-button>
              )}
            </div>
          </div>
          <div class="media">
            <div class="image-grid">
              <div class="image">
                <bs-image class="relative" {...this.parsedbadgeImages[0]} caption=""></bs-image>
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
